}

.messageCloud {
    padding: 10px;
    border-radius: 10px;
    word-break: break-word;
}

    .blueCloud {
        background-color: rgb(145, 173, 209);
        border-radius: 10px 10px 10px 0px;
    }

    .blueCloud.radiusBottomNone {
        border-bottom-left-radius : 0px;
       
    }
    .blueCloud.radiusTopRightNone {
        /* color: white; */
        border-top-left-radius : 0px;
    }


    .greenCloud {
        background-color: rgb(177, 209, 145);
        border-radius: 10px 10px 0px 10px;
    }
    
    .greenCloud.radiusBottomNone {
        border-bottom-right-radius : 0px;
       
    }
    .greenCloud.radiusTopRightNone {
        /* color: white; */
        border-top-right-radius : 0px;
    }